import React, { useState } from 'react';
import people from './data';
import { FaChevronLeft, FaChevronRight, FaQuoteRight } from 'react-icons/fa'; //imported from react icon library

const Review = () => {
  //set state values
  const [index, setIndex] = useState(0);//set to 0 because arrays are zero indexed by default
  const {name, job, image, text} = people[index]//destructure the people array to display specific item, pass in [index] as a value to people rather than hard coding with the index no directly 
 
  //set a conditional statement to return 0 if number is greater than no of values in the array or return the last item if the no is less than zero or return number if it's neither the cases mentioned before
const checkNumber = (number) => {
  if (number > people.length - 1) {
    return 0;
  }

  if (number < 0) {
    return people.length - 1;
  } 
  return number
}

 //add functionality to the prev and next buttons, use setIndex() to access the index and pass in the newIndex as a value that returns a new person in the the array to checkNumber
  const nextPerson = () => {
   setIndex((index) => {
let newIndex = index + 1;
return checkNumber(newIndex);
   })
 }
 
 const prevPerson = () => {
  setIndex((index) => {
let newIndex = index - 1;
return checkNumber(newIndex);
  })
}

//functionality to get a random item on the array
const randomPerson = () => {
let randomNumber = Math.floor(Math.random() * people.length);
//set a conditional statement to avoid repetition of item to be displayed randomly
if (randomNumber === index) {
  randomNumber = index + 1;
}
setIndex(checkNumber(randomNumber));
}

 return <article className='review'>
    <div className='img-container'>
      <img src={image} alt={name} className='person-img'/>
    <span className='quote-icon'>
      <FaQuoteRight />
    </span>
    </div>

    <h4 className='author'>{name}</h4> 
    <p className='job'>{job}</p>
    <p className='info'>{text}</p>
    <div className='button-container'>
      <button className='prev-btn' onClick = {prevPerson}>
        <FaChevronLeft />
      </button>

      <button className='next-btn' onClick = {nextPerson}>
        <FaChevronRight />
      </button>
    </div>
  <button className='random-btn' onClick = {randomPerson}>
        surprise me
      </button>
  </article>;
};

export default Review;
